*{
    margin: 0px;
    padding: 0px;
}
.main_div{
    border: 2px solid rgb(20, 18, 18);
    width: 98.7vw;
    height: 99.5vh;
    background-color: rgb(162, 151, 198);
}
.center_div{
    border-radius: 10px;
    width: 20vw;
    height: 35vw;
    text-align: center;
    margin: auto;
    margin-top: 20vh;
    background-color: white;
    box-shadow: 2px 2px;
}
h1{
    border: 2px solid rgb(22, 19, 19);
    background-color: rgb(146, 66, 146);
    font-size: 2vw;
    color: white;
    border-left: none;
    border-right: none;
}
input{
    font-size: 1.2vw;
    border-top: none;
    border-left: none;
    border-right: none;
    text-align: center;
}
.btn{
    font-size: 2vw;
    height: 3vw;
    width: 3vw;
    border-radius: 3vw;
    cursor: pointer;
    margin-left: 1vw;
    background-color: rgb(146, 66, 146);
    color: white;

}
.btn:hover{
    background-color: rgb(63, 142, 63);
    color: white;
}

ol li{
    margin-left: 2px;
    margin-top: 0.1vw;
    list-style: none;
    color: rgb(146, 66, 146);
    text-align: left;
    font-size: 1.5vw;
    text-transform: capitalize;
}
.todo_style{
    display: flex;
}
button{
    margin-left: 5px;
    font-size: 1.5vw;
    width: 2vw;
    height: 2vw;
    margin-bottom: 2px;
    border-radius: 15vw;
    background-color: rgb(146, 66, 146);
    color: white;
}
button:hover{
    background-color: red;
}